import { EventEmitter, Injectable } from '@angular/core';

export class MessageModel {
    public type: 'success' | 'info' | 'warning' | 'error' | 'blank' | string;
    public title: string;
    public content: string;
}
enum MsgType {
    error = '错误',
    success = '操作成功',
    info = '注意',
    warning = '警告',
}

@Injectable({
    providedIn: 'root'
})
export class MessageService {
    public readonly loadingSubject: EventEmitter<boolean> = new EventEmitter<boolean>();
    public readonly growlSubject: EventEmitter<MessageModel> = new EventEmitter<MessageModel>();

    public showLoading() {
        this.loadingSubject.emit(false);
    }

    public hideLoading() {
        this.loadingSubject.emit(true);
    }

    public success(detail?: string, summary: string = 'success') {
        const content: MessageModel = {
            type: summary,
            title: MsgType[summary],
            content: detail
        };
        this.growlSubject.emit(content);
    }

    public info(detail: string, summary: string = 'info') {
        const content: MessageModel = {
            type: summary,
            title: MsgType[summary],
            content: detail
        };
        this.growlSubject.emit(content);
    }

    public warn(detail: string, summary: string = 'warning') {
        const content: MessageModel = {
            type: summary,
            title: MsgType[summary],
            content: detail
        };
        this.growlSubject.emit(content);
    }

    public error(detail: string, summary: string = 'error') {
        const content: MessageModel = {
            type: summary,
            title: MsgType[summary],
            content: detail
        };
        this.growlSubject.emit(content);
    }
}
